import React from 'react';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
import { Formik } from 'formik';
import axios from 'axios';
import {
  Box,
  Button,
  Checkbox,
  FormHelperText,
  Container,
  Grid,
  Link,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import FacebookIcon from 'src/icons/Facebook';
import GoogleIcon from 'src/icons/Google';
import Page from 'src/components/Page';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    height: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  }
}));

let localData= {};
const getLocalData = (localDataKey) => {
  if (localStorage.getItem(localDataKey) != null){
    localData = JSON.parse(localStorage.getItem(localDataKey));
    return localData;
  }
};
const setLocalData = (localDataKey,localDataValue) => {
    localStorage.setItem(localDataKey, JSON.stringify(localDataValue));
    localData = JSON.parse(localStorage.getItem(localDataKey));
};
const VehicleRegistrationView = () => {
  const classes = useStyles();
  const navigate = useNavigate();
  localData = getLocalData("newRegistrationData");
  console.log(localData);
  return (
    <Page
      className={classes.root}
      title="Login"
    >
      <Box
        display="flex"
        flexDirection="column"
        height="100%"
        justifyContent="top"
      >
        <Container maxWidth="sm">
          <Formik
            initialValues={{
              licenseNumber: '',
              vehicleLicensePlateNumber: '',
              policy: false,
              email: '',
              fullName: '',
              mobileNumber: '',
              password: '',
              confirmPassword: ''
              
            }}
            validationSchema={Yup.object().shape({
              // email: Yup.string().email('Must be a valid email').max(255).required('Email is required'),
              // licenseNumber: Yup.string().max(12).required('Enter a valid license number'),
              licenseNumber: Yup.string().max(255).required('License Number is required'),
              policy: Yup.boolean().oneOf([true], 'This field must be checked')
            })}
            onSubmit={(values) => {
              values.email = localData.email;
              values.firstName = localData.firstName;
              values.lastName = localData.lastName;
              values.mobileNumber = localData.mobileNumber;
              values.password = localData.password;
              values.confirmPassword = localData.confirmPassword;
              axios('http://localhost:5000/addNewRegistration', {
                
                // axios('https://bb1ebabc1d2d.ngrok.io/addNewRegistration', {
                method: 'POST',
                data: values,
                headers: {
                  'Content-Type': 'application/json'
                }
              })
                .then(response => {
                  console.log(response.data);
                  if (response.data.statusCode == 200) {
                    alert("Successfully Registered, Please Login!");
                    navigate('/', { replace: true });
                  }
                })
                .catch(error => {
                  console.error('There was an error!', error);
                });

            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values
            }) => (
              <form onSubmit={handleSubmit}>
                <Box mb={3}>
                  <Typography
                    color="textPrimary"
                    variant="h1"
                  >
                    License and Vehicle Registration
                  </Typography>
                  <Typography
                    color="textSecondary"
                    gutterBottom
                    variant="body2"
                  >
                    Fields with * are mandatory
                  </Typography>
                </Box>
                <TextField
                  error={Boolean(touched.licenseNumber && errors.licenseNumber)}
                  fullWidth
                  required
                  helperText={touched.licenseNumber && errors.licenseNumber}
                  label="License number"
                  margin="normal"
                  name="licenseNumber"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.licenseNumber}
                  variant="outlined"
                />
                {/* <TextField
                  error={Boolean(touched.vehicleLicensePlateNumber && errors.vehicleLicensePlateNumber)}
                  fullWidth
                  helperText={touched.vehicleLicensePlateNumber && errors.vehicleLicensePlateNumber}
                  label="vehicle License Plate number"
                  margin="normal"
                  name="vehicleLicensePlateNumber"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.vehicleLicensePlateNumber}
                  variant="outlined"
                /> */}
                <Box
                  alignItems="center"
                  display="flex"
                  ml={-1}
                >
                  <Checkbox
                    checked={values.policy}
                    name="policy"
                    onChange={handleChange}
                  />
                  <Typography
                    color="textSecondary"
                    variant="body1"
                  >
                    I have read the
                    {' '}
                    <Link
                      color="primary"
                      component={RouterLink}
                      to="#"
                      underline="always"
                      variant="h6"
                    >
                      Terms and Conditions
                    </Link>
                  </Typography>
                </Box>
                {Boolean(touched.policy && errors.policy) && (
                  <FormHelperText error>
                    {errors.policy}
                  </FormHelperText>
                )}
                <Box my={2}>
                  <Button
                    color="primary"
                    disabled={isSubmitting}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    Sign Up
                  </Button>
                </Box>
              </form>
            )}
          </Formik>
        </Container>
      </Box>
    </Page>
  );
};

export default VehicleRegistrationView;
